import React, { useState, useEffect, useRef } from 'react';
import { Table, TableBody, TableCell, TableContainer, TableHead, TableRow, Button } from '@mui/material';
import DealImage from './DealImage';
import TotalListCurrentDeal from './TotalListCurrentDeal';
import TotalList from './TotalList';
import NoteField from './NoteField';
import MyAppBar from './MyAppBar';
import DeleteDealButton from './DeleteDealButton';

function TradingJournal() {
  const [deals, setDeals] = useState([]);
  const [selectedDeal, setSelectedDeal] = useState(null);

  useEffect(() => {
    fetch('http://localhost:8080/deal/getAllDeals')
      .then(response => response.json())
      .then(data => setDeals(data));
  }, []);

  const columns = [
    // { field: 'id', headerName: '#', width: '5%' },
    { field: 'dealNumber', headerName: '#', width: '5%' },
    { field: 'openTime', headerName: 'Time', width: '10%' },
    { field: 'dealType', headerName: 'Type', width: '10%' },
    { field: 'symbol', headerName: 'Symbol', width: '10%' },
    { field: 'openPrice', headerName: 'Open price', width: '10%' },
    { field: 'lot', headerName: 'Lot', width: '10%' },
    { field: 'stopLoss', headerName: 'SL', width: '15%' },
    { field: 'takeProfit', headerName: 'TP', width: '15%' },
    { field: 'profitDollars', headerName: '$Profit', width: '20%' },
  ];

  const handleTableRowClick = (event, deal) => {
    setSelectedDeal(deal);
  };

  const handleNoteUpdate = (deal, newNote) => {
    if (deal) {
      fetch(`http://localhost:8080/deal/updateDeal/${deal.id}`, {
        method: 'PUT',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ ...deal, note: newNote }),
      })
        .then(response => response.json())
        .then(data => {
          setDeals(deals.map(d => (d.id === data.id ? data : d)));
        })
        .catch(error => console.error(error));
    }
  };

  const handleDeleteDeal = (deal) => {
    if (deal) {
      const confirmation = window.confirm('Delete deal?');
      if (confirmation) {
        fetch(`http://localhost:8080/deal/deleteDeal/${deal.id}`, {
          method: 'DELETE',
          headers: { 'Content-Type': 'application/json' },
        })
          .then(() => {
            setDeals(deals.filter((d) => d.id !== deal.id));
            setSelectedDeal(null);
            // alert('Deal deleted successfully.');
          })
          .catch((error) => console.error(error));
      }
    }
  };
  

  return (
    <div style={{ marginTop: '40px'}} >
      <MyAppBar/>
      {/* <DeleteDealButton selectedDeal={selectedDeal} /> */}
    <div style={{ backgroundColor: '#ffecb3' , display: 'flex', height: '100%', width: '100%' }}>
      <div style={{ backgroundColor: '#f0f0f0' , borderRadius: '15px'
         , margin: '4px', marginBottom: '0', width: '45%', height: '93vh', padding: '0px' }}>
        <TableContainer style={{ height: '100%' }}>
          <Table>
            <TableHead>
              <TableRow>
                {columns.map(column => (
                  <TableCell key={column.field} 
                    style={{ width: column.width, fontSize: 11
                    , fontWeight: 'bold', color: '#424242' }}>

                    {column.headerName}
                  </TableCell>
                ))}
              </TableRow>
            </TableHead>
            <TableBody>
              {deals.map(deal => (
                <TableRow
                key={deal.id}
                onClick={(event) => handleTableRowClick(event, deal)}
                style={{
                  cursor: "pointer",
                  backgroundColor:
                    selectedDeal && selectedDeal.id === deal.id ? "#ccff90" : "",
                }}>
                  {columns.map(column => (
                    <TableCell key={column.field} 
                      style={{ width: column.width , fontSize: 11, 
                      color: '#424242' }}>
                      {deal[column.field]}
                    </TableCell>
                  ))}

                  <button style={{ backgroundColor: 'transparent'
                    , border: 'none', color: '#ef5350', fontSize:'2em'
                    , cursor: 'pointer', fontWeight: 'bold'}}
                    onClick={() => handleDeleteDeal(deal)}
                    ></button>

                </TableRow>
              ))}
            </TableBody>
          </Table>
          </TableContainer>
    </div>
    <div style={{ height: '100%', width: '44%' , display: 'flex', flexDirection: 'column', alignItems: 'flex-start', justifyContent: 'flex-start'
          , marginRight: '0px', marginLeft: '0px' , margin: '4px'}}>
      <NoteField selectedDeal={selectedDeal} handleNoteUpdate={handleNoteUpdate} />

      <DealImage selectedDeal={selectedDeal} />
    </div>


    <div style={{ margin: '4px', display: 'grid', justifyContent: 'space-between'}}>
      <div>
        <TotalListCurrentDeal selectedDeal={selectedDeal} />
      </div>
      <div>
        <TotalList />
      </div>
    </div>

  </div>

  </div>

  );
}
export default TradingJournal;