import React from 'react';
import TotalList from './TotalList';

const TotalListCurrentDeal = ({ selectedDeal }) => {
  if (!selectedDeal) {
    return <div style={{ color: '#424242', textAlign: 'center', fontSize: '1em' 
    , fontWeight: 'bold' }}>About the deal</div>;
  }

  const {
    openTime,
    symbol,
    dealType,
    leverage,
    openPrice,
    lot,
    margin,
    stopLoss,
    takeProfit,
    profitDollars,
    profitPercents,
    commissions,
    closePrice,
    closeTime,
    contract,
    contractSize
  } = selectedDeal;

  return (
    <div>
      <table style={{ fontSize: '0.8em', lineHeight: '1.2em'
          , padding: '2px', color: '#424242', backgroundColor: '#d7ccc8' , borderRadius: '15px'}}>
        <caption style={{ textAlign: 'center', fontSize: '1.2em' 
                  , fontWeight: 'bold' }}>About the deal</caption>
        <tbody>
          <tr>
            <td style={{ textAlign: 'left' }}>Open Time:</td>
            <td style={{ textAlign: 'right' }}>{openTime}</td>
          </tr>
          <tr>
            <td style={{ textAlign: 'left' }}>Symbol:</td>
            <td style={{ textAlign: 'right' }}>{symbol}</td>
          </tr>
          <tr>
            <td style={{ textAlign: 'left' }}>Deal Type:</td>
            <td style={{ textAlign: 'right' }}>{dealType}</td>
          </tr>
          <tr>
            <td style={{ textAlign: 'left' }}>Leverage:</td>
            <td style={{ textAlign: 'right' }}>{leverage}</td>
          </tr>
          <tr>
            <td style={{ textAlign: 'left' }}>Open Price:</td>
            <td style={{ textAlign: 'right' }}>{openPrice}</td>
          </tr>
          <tr>
            <td style={{ textAlign: 'left' }}>Lot:</td>
            <td style={{ textAlign: 'right' }}>{lot}</td>
          </tr>
          <tr>
            <td style={{ textAlign: 'left' }}>Margin:</td>
            <td style={{ textAlign: 'right' }}>{margin}</td>
          </tr>
          <tr>
            <td style={{ textAlign: 'left' }}>Stop Loss:</td>
            <td style={{ textAlign: 'right' }}>{stopLoss}</td>
          </tr>
          <tr>
            <td style={{ textAlign: 'left' }}>Take Profit:</td>
            <td style={{ textAlign: 'right' }}>{takeProfit}</td>
          </tr>
          <tr>
            <td style={{ textAlign: 'left' }}>Profit Dollars:</td>
            <td style={{ textAlign: 'right' }}>{profitDollars}</td>
          </tr>
          <tr>
            <td style={{ textAlign: 'left' }}>Profit Percent:</td>
            <td style={{ textAlign: 'right' }}>{profitPercents}</td>
          </tr>
          <tr>
            <td style={{ textAlign: 'left' }}>Commissions:</td>
            <td style={{ textAlign: 'right' }}>{commissions}</td>
          </tr>
          <tr>
            <td style={{ textAlign: 'left' }}>Close Price:</td>
            <td style={{ textAlign: 'right' }}>{closePrice}</td>
          </tr>
          <tr>
            <td style={{ textAlign: 'left' }}>Close Time:</td>
            <td style={{ textAlign: 'right' }}>{closeTime}</td>
          </tr>
          <tr>
            <td style={{ textAlign: 'left' }}>Contract:</td>
            <td style={{ textAlign: 'right' }}>{contract}</td>
          </tr>
          <tr>
            <td style={{ textAlign: 'left' }}>Contract Size:</td>
            <td style={{ textAlign: 'right' }}>{contractSize}</td>
          </tr>
        </tbody>
      </table>
      
    </div>
  );
};

export default TotalListCurrentDeal;
