import React, { useState, useEffect } from 'react';

function TotalList() {
  const [totalList, setTotalList] = useState(null);

  useEffect(() => {
    fetch('http://localhost:8080/deal/getTotalInfo')
      .then(response => response.json())
      .then(data => setTotalList(data));
  }, []);

  if (!totalList) {
    return <div>Loading...</div>;
  }

  return (
    <div>
      <table style={{ width: '100%', fontSize: '0.8em', lineHeight: '1.2em'
        , padding: '2px', backgroundColor: '#d7ccc8' , color: '#424242'
        , borderRadius: '15px'}}>
        <caption style={{ textAlign: 'center', fontSize: '1.2em' 
                  , fontWeight: 'bold' }}>Total</caption>
        <tbody>
          <tr>
            <td style={{ textAlign: 'left' }}>Deals:</td>
            <td style={{ textAlign: 'right' }}>{totalList.totalDeals}</td>
          </tr>
          <tr>
            <td style={{ textAlign: 'left' }}>Winning:</td>
            <td style={{ textAlign: 'right' }}>{totalList.totalWinDeals}</td>
          </tr>
          <tr>
            <td style={{ textAlign: 'left' }}>Losing:</td>
            <td style={{ textAlign: 'right' }}>{totalList.totalWasteDeals}</td>
          </tr>
          <tr>
            <td style={{ textAlign: 'left' }}>Profit $:</td>
            <td style={{ textAlign: 'right' }}>{totalList.totalProfitDollars}</td>
          </tr>
          <tr>
            <td style={{ textAlign: 'left' }}>Profit %:</td>
            <td style={{ textAlign: 'right' }}>{totalList.totalProfitPercents}</td>
          </tr>
        </tbody>
      </table>
    </div>
  );
}

export default TotalList;
