import React, { useState, useEffect, useRef } from 'react';

const NoteField = ({ selectedDeal, handleNoteUpdate }) => {
  const [note, setNote] = useState('');
  const delayedNoteUpdate = useRef(null);

  useEffect(() => {
    if (selectedDeal) {
      setNote(selectedDeal.note || '');
    }
  }, [selectedDeal]);

  const handleNoteChange = (event) => {
    const newNote = event.target.value;
    setNote(newNote);
    clearTimeout(delayedNoteUpdate.current);
    delayedNoteUpdate.current = setTimeout(() => {
      if (selectedDeal) {
        handleNoteUpdate(selectedDeal, newNote);
      }
    }, 5000);
  };

  return (
    <textarea
      placeholder="NOTE"
      value={note}
      onChange={handleNoteChange}
      onBlur={() => handleNoteUpdate(selectedDeal, note)}
      
      style={{ backgroundColor: '#e0f7fa', borderRadius: '15px' 
        , width: '100%', height: '45vh' , borderColor: 'white'
        , padding: '5px', boxSizing: 'border-box', color: '#424242'}}
        
      readOnly={!selectedDeal}
    />
  );
};

export default NoteField;
