import React, { useState, useEffect } from 'react';
import styled from './ButtonStyles.css';
import './styles.css';

async function fetchImages(selectedDeal) {
  const urls = [];
  let i = 0;
  while (true) {
    const url = `http://localhost:8080/deal/${selectedDeal.id}/getImage/${i}`;
    const response = await fetch(url);
    if (response.ok) {
      const blob = await response.blob();
      if (blob.size !== 0) {
        urls.push(URL.createObjectURL(blob));
        i++;
      } else {
        break;
      }
    } else {
      break;
    }
  }
  return urls;
}

function DealImage({ selectedDeal }) {
  const [images, setImages] = useState([]);
  const [open, setOpen] = useState(false);
  const [selectedIndex, setSelectedIndex] = useState(0);

  useEffect(() => {
    if (selectedDeal) {
      fetchImages(selectedDeal)
        .then(urls => setImages(urls))
        .catch(error => console.error(error));
    }
  }, [selectedDeal]);

  const handleClickOpen = (index) => {
    setSelectedIndex(index);
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  const handleDownloadClick = async () => {
    const input = document.createElement('input');
    input.type = 'file';
    input.accept = 'image/*';
    input.onchange = async () => {
      const file = input.files[0];
      const formData = new FormData(); // create new FormData object
      formData.append('image', file); // append the selected file to FormData
      const url = `http://localhost:8080/deal/${selectedDeal.id}/addImageToDeal`;
      const options = {
        method: 'POST',
        body: formData,
      };
      const response = await fetch(url, options);
      if (response.ok) {
        const urls = await fetchImages(selectedDeal);
        setImages(urls);
      }
    };
    input.click();
  };
  
  const handleDelete = () => {
    const url = `http://localhost:8080/deal/${selectedDeal.id}/deleteImage/${selectedIndex}`;
    fetch(url, {
      method: 'DELETE',
    })
      .then(response => {
        if (response.ok) {
          const newImages = [...images];
          newImages.splice(selectedIndex, 1);
          setImages(newImages);
          handleClose();
        } else {
          console.error(`Failed to delete image: ${response.status} - ${response.statusText}`);
        }
      })
      .catch(error => {
        console.error(`Failed to delete image: ${error}`);
      });
  };

  const ButtonPlus = styled.button;

  const handleContentClick = (event) => {
    event.stopPropagation();
  };

return (

  <div style={{ borderRadius: '15px', marginTop: '4px', display: 'grid'
    , gridTemplateColumns: 'repeat(2, 1fr)', gridGap: '4px'
    , height: '47vh', width: '100%', overflowX: 'hidden' 
    , }}>

  {images.length > 0 &&
    images.map((url, index) => (
      <div key={index} 
            onClick={() => handleClickOpen(index)} 
            style={{ cursor: 'pointer' }}>
        <img src={url} alt={`Image ${index + 1}`} 
          style={{ width: '100%', height: '100%', objectFit: 'cover' }} /> 
      </div>
    ))}
  {images.length === 0 && (
    <div style={{ width: '100%', height: '30%' }}>
      <button onClick={handleDownloadClick} className="button_plus">
        <span className="button_plus_icon"></span>
      </button>
    </div>
  )}
  {images.length > 0 && (
    <div style={{ width: '100%', height: '100%', minHeight: '10vh' }}>
    <button onClick={handleDownloadClick} className="button_plus">
      <span className="button_plus_icon"></span>
    </button>
    </div>
  )}

    {open && selectedIndex !== null && (
      <>
        <div className="image-dialog" onClick={handleClose}>

          <button className="close-button" onClick={handleClose}
                  style={{ backgroundColor: 'transparent'
                  , border: 'none', fontSize:'2em'
                  , cursor: 'pointer', fontWeight: 'bold'}}>
            
          </button>
        
          <img
            src={images[selectedIndex]}
            alt={`Image ${selectedIndex + 1}`}
            onClick={handleContentClick}
            style={{ objectFit: 'contain', maxHeight: '100%', maxWidth: '100%' }}
          />

          <button className="delete-button" 
                  style={{ backgroundColor: 'transparent'
                  , border: 'none', color: '#ef5350', fontSize:'3em'
                  , cursor: 'pointer', fontWeight: 'bold'}} 
                  onClick={handleDelete}>
            
          </button>

        </div>
      </>
    )}

  </div>
  );
}

  export default DealImage;
