import React, { useState } from 'react';
import AddDealButton from './AddDealButton';
import DeleteDealButton from './DeleteDealButton';
import LogOutButton from './LogOutButton';

const MyAppBar = () => {
    
  return (
    <div style={{ height: '40px', backgroundColor: '#40c4ff', position: 'absolute', top: 0, left: 0, right: 0 }}>
      <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', padding: '0px' }}>
        <div>
          <button style={{ backgroundColor: 'transparent',  border: 'none', margin:'0px',  paddingRight:'10px', padding:'0px', marginLeft: '6px', fontSize:'1.4em', color: 'white', cursor: 'pointer' }}></button>
          <button style={{ backgroundColor: 'transparent', border: 'none', paddingBlock:'0px' }}><AddDealButton/></button>
          {/* <button style={{ backgroundColor: 'transparent', border: 'none', paddingBlock:'0px' }}><DeleteDealButton/></button> */}
          
        </div>
        <div>
          {/* <button style={{ backgroundColor: 'transparent', border: 'none', color: 'white', cursor: 'pointer' }}>Button 3</button>
          <button style={{ backgroundColor: 'transparent', border: 'none', color: 'white', cursor: 'pointer' }}>Button 4</button> */}
          <button style={{ backgroundColor: 'transparent', border: 'none', paddingBlock:'0px', paddingTop:'6px' }}><LogOutButton/></button>
        </div>
      </div>
    </div>
  );
};

export default MyAppBar;

