import React, { useState } from 'react';
import { Button, Modal, TextField } from '@mui/material';

const AddDealButton = () => {
  const [open, setOpen] = useState(false);
  const [formData, setFormData] = useState({
    openTime: '',
    symbol: '',
    dealType: '',
    leverage: '',
    openPrice: '',
    lot: '',
    stopLoss: '',
    takeProfit: '',
    commissions: '',
    closePrice: '',
    closeTime: '',
  });

  const handleOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  const handleSubmit = () => {
    fetch('http://localhost:8080/deal/addDeal', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify(formData),
    })
      .then((response) => {
        if (response.ok) {
          // Success
          handleClose();
        } else {
          // Error
          alert('Failed to add deal');
        }
      })
      .catch((error) => {
        console.error(error);
        alert('Failed to add deal');
      });
  };

  const handleChange = (event) => {
    setFormData({
      ...formData,
      [event.target.name]: event.target.value,
    });
  };

  return (
    <div>
      <Button style={{color: '#e0e0e0', backgroundColor:'#4caf50'}}
        size="small" variant="contained" color="success" onClick={handleOpen}>
        Add Deal
      </Button>
      <Modal open={open} onClose={handleClose}>
        <div style={{ height: '80%', lineHeight:'0', position: 'absolute', top: '50%', left: '50%', transform: 'translate(-50%, -50%)', backgroundColor: 'white', padding: '20px', borderRadius: '5px' }}>
          <h3>Add Deal</h3>
          <form onSubmit={handleSubmit} style={{}}>
            <TextField fullWidth size="small" label="Open Time" name="openTime" value={formData.openTime} onChange={handleChange} />
            <TextField fullWidth size="small" label="Symbol" name="symbol" value={formData.symbol} onChange={handleChange} />
            <TextField fullWidth size="small" label="Deal Type" name="dealType" value={formData.dealType} onChange={handleChange} />
            <TextField fullWidth size="small" label="Leverage" name="leverage" value={formData.leverage} onChange={handleChange} />
            <TextField fullWidth size="small" label="Open Price" name="openPrice" value={formData.openPrice} onChange={handleChange} />
            <TextField fullWidth size="small" label="Lot" name="lot" value={formData.lot} onChange={handleChange} />
            <TextField fullWidth size="small" label="Stop Loss" name="stopLoss" value={formData.stopLoss} onChange={handleChange} />
            <TextField fullWidth size="small" label="Take Profit" name="takeProfit" value={formData.takeProfit} onChange={handleChange} />
            <TextField fullWidth size="small" label="Commissions" name="commissions" value={formData.commissions} onChange={handleChange} />
            <TextField fullWidth size="small" label="Close Price" name="closePrice" value={formData.closePrice} onChange={handleChange} />
            <TextField fullWidth size="small" label="Close Time" name="closeTime" value={formData.closeTime} onChange={handleChange} />
            <Button type="submit" variant="contained" style={{ marginTop: '10px' }}>
              Confirm
            </Button>
          </form>
        </div>
      </Modal>
    </div>
  );
};

export default AddDealButton;
