import React from 'react';
import Button from '@mui/material/Button';

const LogOutButton = () => {
  const handleLogout = () => {
    window.location.href = '/logout'; // Replace '/logout' with your desired logout URL
  };

  return (
    <Button style={{color: '#e0e0e0', backgroundColor:'#424242'}}
        variant="contained" size='small' onClick={handleLogout}>
      Log out
    </Button>
  );
};

export default LogOutButton;
